<script>
// @ is an alias to /src
import { reactive, ref } from "vue";
import Header from "../../components/Header/index.vue";
import Footer from "../../components/Footer/index.vue";
import Routes from "../../services/Routes";
import * as moment from "moment";
import Swal from "sweetalert2";
import AOS from "aos";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      menu: true,
      Vouchers: false,
    };
  },
  methods: {
    getPercent(n) {
      let val = (n / 1).toFixed(2).replace(".", ",");
      return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
    getDescription: function (name) {
      if (name.length > 250) {
        return name.substring(0, 250) + "...";
      }
      return name;
    },
    getURL(item) {
      var a = "";
      for (var obj in item) {
        a = item[obj];
        if (a.includes("https", 0) == true) {
          return a;
        } else {
          return null;
        }
      }
    },
    goTO(url) {
      this.$router.push(url);
    },
    getCourses() {
      let data = JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
        ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
        : 0;
      Routes.getVouchers(data)
        .then((r) => {
          this.Vouchers = r.data.DATA;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.getCourses();
    AOS.init();
  },

  created() {},
  setup() {},
};
</script>

